let cachedCoordinates = null;

async function getUserIPAddress() {
    const response = await fetch('https://api64.ipify.org?format=json');
    const data = await response.json();
    return data.ip;
};  

async function fallbackGetCoordinates() {
    try {
        const userIP = await getUserIPAddress();
        const response = await fetch(`https://8t1j14pxk9.execute-api.sa-east-1.amazonaws.com/geo?ip=${userIP}`);
        if (!response.ok) {
            const error = new Error(response.status);
            error.status = response.status;
            throw error;
        };
        const data = await response.json();
        return [data.latitude.toString(), data.longitude.toString()];
    } catch(error) {
        console.error('Error fetching fallback coordinates:', error);
        cachedCoordinates = null;
        return null
    };
};

async function getCoordinates() {
    const options = {
        enableHighAccuracy: true,
        timeout: 6000,
        maximumAge: 0
    };

    const getPosition = (options) => {
        return new Promise((resolve, reject) => {
            navigator.geolocation.getCurrentPosition(resolve, (error) => {
                console.log('Geolocation error details:', {
                    code: error.code,
                    message: error.message,
                    PERMISSION_DENIED: error.PERMISSION_DENIED,
                    POSITION_UNAVAILABLE: error.POSITION_UNAVAILABLE,
                    TIMEOUT: error.TIMEOUT
                });
                reject(error);
            }, options);
        });
    };

    try {
        if (cachedCoordinates) {
            console.log('Cached coordinates found, returning cached coordinates');
            return cachedCoordinates;
        } else {
            const position = await getPosition(options);
            const { latitude, longitude } = position.coords;
            cachedCoordinates = [latitude.toString(), longitude.toString()];
            return cachedCoordinates;
        }
    } catch (error) {
        if (error.code === 2) {
            console.warn('Geolocation failed, using fallback method');
            return await fallbackGetCoordinates();
        } else {
            cachedCoordinates = null;
            return null;
        };
    };
};

export { getCoordinates }