import './styles.css';
import { main } from './mainLogicModule.js';

function debounce(func, wait) {
    let timeout;
    return function(...args) {
        clearTimeout(timeout);
        timeout = setTimeout(() => func.apply(this, args), wait)
    };
};

window.addEventListener('load', () => {
    main();
    let initialHeight = window.innerHeight;
    let initialOrientation = window.orientation;

    const debounceResizeHandler = debounce(() => {
        let currentHeight = window.innerHeight;
        let currentOrientation = window.orientation;
        if (initialOrientation !== currentOrientation) {
            main();
        } else {
            if (Math.abs(currentHeight - initialHeight) > 150) {
                if(currentOrientation === 90 || currentOrientation === -90) {
                    if (Math.abs(currentHeight - initialHeight) > 300) {
                        main();
                    };
                } else {
                    main();
                }
            }
        }
        initialHeight = currentHeight;
        initialOrientation = currentOrientation;
    }, 200);
    window.addEventListener('resize', debounceResizeHandler);
});